import { KameleoonStorageKey } from '../storage';
import { TargetingType } from '../targeting';
import { KameleoonException } from './types';

export const ERROR_MESSAGES = {
  [KameleoonException.JSONParse]: (err: unknown) =>
    `Couldn't parse JSON variable: ${err}`,
  [KameleoonException.NumberParse]: (value: string) =>
    `It's not possible to parse value ${value} to Number`,
  [KameleoonException.Initialization]: () =>
    "It seems that the client wasn't properly initialized, make sure to run `initialize` method before invoking other methods",
  [KameleoonException.StorageInitialization]: () =>
    "There was an error while initializing React Native SDK storage, it seems that the storage library dependency wasn't installed",
  [KameleoonException.EventSourceInitialization]: () =>
    "There was an error while initializing Real Time Update service, it seems that the event source library dependency wasn't installed",
  [KameleoonException.VariationNotFound]: () =>
    "Couldn't find experiment variation",
  [KameleoonException.FeatureFlagConfigurationNotFound]: (
    featureKey: string,
    visitorCode: string,
  ) =>
    `No feature flag with key ${featureKey} was found for ${visitorCode} visitorCode.`,
  [KameleoonException.ExperimentConfigurationNotFound]: (
    experimentId: number,
    visitorCode: string,
  ) =>
    `No experiment with id ${experimentId} was found for ${visitorCode} visitorCode.`,
  [KameleoonException.NotTargeted]: (visitorCode: string) =>
    `Visitor code ${visitorCode} is not targeted`,
  [KameleoonException.NotAllocated]: (visitorCode: string) =>
    `Visitor with visitor code ${visitorCode} is in unallocated traffic, there is no variation id to be assigned to the visitor.`,
  [KameleoonException.VisitorCodeMaxLength]: () =>
    'Visitor code can not be more than 255 characters long',
  [KameleoonException.StorageWrite]: (err: unknown) =>
    `Couldn't update storage for kameleoonClient: ${err}`,
  [KameleoonException.StorageRead]: (key: KameleoonStorageKey) =>
    `No data found in storage under ${key} key`,
  [KameleoonException.StorageParse]: (err: unknown, key: KameleoonStorageKey) =>
    `Couldn't parse ${key} storage data, the data may be corrupted. Error: ${err}`,
  [KameleoonException.ClientConfiguration]: () =>
    "Couldn't retrieve client configuration from Kameleoon Api",
  [KameleoonException.TargetingCondition]: (targetingType: TargetingType) =>
    `${targetingType} targeting condition is not yet supported.`,
  [KameleoonException.AmongValuesCheck]: (err: unknown, value: string) =>
    `Couldn't parse value "${value}": ${err}`,
  [KameleoonException.RemoteData]: () =>
    `Couldn't retrieve data from Kameleoon server`,
};
